/* Add Meeting Page Styles */
.add-meeting-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  min-height: 100vh;
}

/* Page Header */
.page-header {
  background: #D4AF5F;;
  color: white !important;
  padding: 1.5rem 2rem;
  border-radius: 12px 12px 0 0;
  margin-bottom: 2rem;
  text-align: center;
}

.page-title {
  font-size: 2rem;
  font-weight: 700;
  color: white !important;
  margin-bottom: 0.5rem;
}

.page-description {
  color: white !important;
  font-size: 1.1rem;
}

/* Alerts */
.alert {
  padding: 1rem;
  border-radius: 0.5rem;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  cursor: pointer;
  transition: opacity 0.2s;
}

.alert:hover {
  opacity: 0.8;
}

.alert-success {
  background-color: #d1fae5;
  color: #065f46;
  border: 1px solid #a7f3d0;
}

.alert-error {
  background-color: #fee2e2;
  color: #991b1b;
  border: 1px solid #fca5a5;
}

.alert-icon {
  flex-shrink: 0;
}

/* Form Sections */
.form-sections {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.form-section {
  background: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}

.section-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 1.5rem;
  border-bottom: 2px solid #f3f4f6;
  padding-bottom: 0.75rem;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  border-bottom: 2px solid #f3f4f6;
  padding-bottom: 0.75rem;
}

/* Form Grid */
.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.form-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
}

/* Form Groups */
.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-label {
  font-weight: 500;
  color: #5F646D;
  font-size: 0.875rem;
}

.form-input,
.form-textarea,
.form-select {
  padding: 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  transition: border-color 0.2s, box-shadow 0.2s;
  background: #fff;
}

.form-input:focus,
.form-textarea:focus,
.form-select:focus {
  outline: none;
  border-color: #D4AA3A;
  box-shadow: 0 0 0 3px rgba(212, 170, 58, 0.1);
}

.form-input.error,
.form-textarea.error,
.form-select.error {
  border-color: #ef4444;
}

.form-textarea {
  resize: vertical;
  min-height: 80px;
}

/* Error Messages */
.error-message {
  color: #ef4444;
  font-size: 0.75rem;
  margin-top: 0.25rem;
}

/* Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 0.5rem;
  font-weight: 500;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s;
  text-decoration: none;
  justify-content: center;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn-primary {
  background-color: #D4AA3A;
  color: #FFFFFF;
}

.btn-primary:hover:not(:disabled) {
  background-color: #B8941F;
}

.btn-secondary {
  background-color: transparent;
  color: #1B1D21 !important;
  border: 1px solid #1B1D21;
}

.btn-secondary:hover:not(:disabled) {
  background-color: #1B1D21;
  border-color: #1B1D21;
  color: #FFFFFF !important;
}

.btn-danger {
  background-color: #ef4444;
  color: white;
}

.btn-danger:hover:not(:disabled) {
  background-color: #dc2626;
}

.btn-sm {
  padding: 0.5rem 1rem;
  font-size: 0.75rem;
}

.btn-icon {
  flex-shrink: 0;
}

.btn-spinner {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Form Actions */
.form-actions {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid #e5e7eb;
}

/* Empty States */
.empty-state {
  text-align: center;
  padding: 2rem;
  color: #6b7280;
}

.empty-icon {
  margin-bottom: 1rem;
  color: #d1d5db;
}

.empty-text {
  font-size: 0.875rem;
}

/* Attendees Section */
.attendees-list {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.attendee-item {
  border: 1px solid #e5e7eb;
  border-radius: 0.5rem;
  padding: 1.5rem;
  background: #f9fafb;
}

.attendee-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.attendee-title {
  font-size: 1rem;
  font-weight: 600;
  color: #374151;
  margin: 0;
}

.attendee-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

/* User Search */
.search-input-container {
  position: relative;
}

.user-search-results {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  z-index: 10;
  max-height: 200px;
  overflow-y: auto;
}

.user-search-item {
  padding: 0.75rem;
  cursor: pointer;
  border-bottom: 1px solid #f3f4f6;
  transition: background-color 0.2s;
}

.user-search-item:hover {
  background-color: #f9fafb;
}

.user-search-item:last-child {
  border-bottom: none;
}

.user-info {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.user-name {
  font-weight: 500;
  color: #374151;
}

.user-department {
  font-size: 0.75rem;
  color: #6b7280;
}

.selected-user {
  margin-top: 0.5rem;
  padding: 0.5rem;
  background: #dbeafe;
  border-radius: 0.25rem;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.selected-user-name {
  font-weight: 500;
  color: #1e40af;
}

.selected-user-department {
  font-size: 0.75rem;
  color: #3b82f6;
}

/* Post Creation Sections */
.post-creation-sections {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

/* Recommendations */
.recommendations-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.recommendations-form .form-group {
  margin-bottom: 0;
}

.recommendations-form button {
  align-self: flex-start;
  min-width: 120px;
}

.recommendations-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.recommendations-list .empty-state {
  text-align: center;
  padding: 2rem;
  background: #f9fafb;
  border-radius: 0.5rem;
  border: 2px dashed #d1d5db;
}

.recommendations-list .empty-text {
  color: #6b7280;
  font-style: italic;
  margin: 0;
}

.recommendation-item {
  display: flex;
  gap: 0.75rem;
  padding: 1rem;
  background: #f9fafb;
  border-radius: 0.5rem;
  border-left: 4px solid #3b82f6;
}

.recommendation-number {
  font-weight: 600;
  color: #3b82f6;
  min-width: 1.5rem;
}

.recommendation-text {
  color: #374151;
  line-height: 1.5;
  flex: 1;
}

.recommendation-assignee {
  margin-top: 8px;
  padding: 6px 12px;
  background: #e3f2fd;
  border-radius: 4px;
  border-left: 3px solid #2196f3;
  font-size: 0.9rem;
}

.assignee-label {
  font-weight: 500;
  color: #1976d2;
  margin-right: 8px;
}

.assignee-name {
  color: #1565c0;
  font-weight: 600;
}

/* Attachments */
.attachments-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.attachments-form .form-row {
  margin-bottom: 1rem;
}

.attachments-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.attachment-item {
  padding: 1rem;
  background: #f9fafb;
  border-radius: 0.5rem;
  border: 1px solid #e5e7eb;
}

.attachment-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.attachment-name {
  font-weight: 500;
  color: #374151;
}

.attachment-type {
  font-size: 0.75rem;
  color: #6b7280;
  background: #e5e7eb;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
}

.attachment-details {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  font-size: 0.75rem;
  color: #6b7280;
}

.attachment-description {
  font-weight: 500;
}

.attachment-source {
  background: #dbeafe;
  color: #1e40af;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
}



.add-meeting-page.rtl .recommendation-item {
  border-left: none;
  border-right: 4px solid #3b82f6;
}

.add-meeting-page.rtl .user-search-results {
  left: auto;
  right: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .add-meeting-page {
    padding: 1rem;
  }

  .form-grid {
    grid-template-columns: 1fr;
  }

  .form-row {
    grid-template-columns: 1fr;
  }

  .section-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }

  .recommendations-form {
    flex-direction: column;
    align-items: stretch;
  }

  .attendee-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }

  .attachment-header {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }

  .attachment-details {
    flex-direction: column;
    gap: 0.5rem;
  }
}

@media (max-width: 480px) {
  .page-title {
    font-size: 1.5rem;
  }

  .form-section {
    padding: 1rem;
  }

  .btn {
    padding: 0.625rem 1.25rem;
    font-size: 0.8rem;
  }
}
